import React from 'react'
import FooterMidList from './FooterMidList'
import { middleList } from '../../constants'
import { FlagIndia } from '../../assests'
import { logo } from '../../assests'
import LanguageIcon from '@mui/icons-material/Language';

function FooterMiddle() {
  return (
    <div className='w-full bg-amazon_light text-white'>
        <div className='w-full border-b-[1px] border-gray-500 py-10'>
            <div className='max-w-5xl mx-auto text-gray-300 '>
                <div className='w-full grid grid-cols-4 place-items-center items-start'>
                {
                    middleList.map((item)=>(
                        <FooterMidList 
                            key={item._id}
                            title={item.title}
                            listItem={item.ListItem}
                        />
                    ))
                }
                </div>
            </div>
        </div>

        <div className='w-full flex gap-6 items-center justify-center py-6'>
            <div>
                <img className='w-20 pt-3' src={logo} alt="logo"></img>
            </div>
            <div className='flex gap-2'>
                <p className='flex gap-1 items-center justify-center border border-gray-500 hover:border-amazon_yellow cursor-pointer duration-200 px-2 py-1'><LanguageIcon/>English</p>
            </div>
            <div className='flex gap-1 items-center justify-center border border-gray-500 hover:border-amazon_yellow cursor pointer duration-200 px-2 py-1'>
                <img className='w-6' src={FlagIndia} alt="flag"></img>
                <p>India</p>
            </div>
        </div>
    </div>
  ) 
}

export default FooterMiddle
